export const uploadFile = async (formData: FormData) => {
  try {
    const config = {
      headers: {
        'content-type': 'multipart/form-data'
      }
    };
    const res: AxiosResponse = await axios.post(
      '/upload-file',
      formData,
      config
    );
    return res;
  } catch (error) {
    throw new Error(error);
  }
};

